// COLORS
$color-primary: #107e89;
$color-primary-light: #0dabbb;
$color-primary-dark: #034d57;

$color-secondary-light: #ffb900;
$color-secondary-dark: #ff7730;

$color-tertiary-light: #2998ff;
$color-tertiary-dark: #5643fa;

$color-grey-light-1: #f7f7f7;
$color-grey-light-2: #eee;

$color-grey-dark: #777;
$color-grey-dark-2: #999;
$color-grey-dark-3: #333;

$color-white: #fff;
$color-black: #000;


// FONT
$default-font-size: 1.6rem;


// GRID
$grid-width: 114rem;
$gutter-vertical: 8rem;
$gutter-horizontal: 6rem;

// BUTTONS
$btn-background-color:#2a3436;
$btn-border-color:$color-primary-dark;

$btn-gold-color:#ffc60b;
$btn-gold-text:#444444;

$skill-border-color:#ffc60b;